import React from 'react';
import styles from './EditorForm.scss';
import classNames from 'classnames/bind';
import axios from 'axios';
import Button from 'components/common/Button';

const cx = classNames.bind(styles);

const EditorForm = ({ onChangeImage, history }) => {

  const handleClickImageButton = () => {
    this.imageInput.click();
  }

  const handleUpload = async () => {
    const token = await localStorage.getItem('token');

    const image = this.imageInput.files[0];

    const formData = new FormData();

    formData.append('image', image);
    formData.append('caption', this.caption.value);

    const config = {
      headers: {
        'Authorization': `JWT ${token}`,
        'Content-Type': 'application/x-www-form-urlencoded'
      }
    };

   

    try {
      await axios.post(`/gallery/`, formData, config);
      window.location.href = "/";
    } catch (e) {
      console.log(e);
    }
  }



  return (
    <div className={cx('editor-form')}>
      <div className={cx('title')}>
         
      </div>
      <div className={cx('form-value')}>
        <input
          type="file"
          name="image"
          style={{ display: 'none' }}
          ref={el => this.imageInput = el} />
        <Button
          theme="select-image"
          onClick={handleClickImageButton}> </Button>
      </div>
      <div className={cx('form-value')}>
        <div className={cx('label')}>
            
        </div>
        <input 
          type="text" 
          name="caption"
          ref={el => this.caption = el} />
      </div>
      <Button theme="submit" onClick={handleUpload}> </Button>
    </div>
  );
}

export default EditorForm;


